.page-wrapper .nav-wrapper .menu-toggle .bar {
  margin-right: 15px !important;
  width: 25px;
  height: 3px;
  z-index: 999;
  top: 5px;
  position: relative;
  background-color: #10a3ff;
  margin: 5px auto;
  -webkit-transition: all 0.1s ease-in-out;
  transition: all 0.1s ease-in-out;
}

.page-wrapper .nav-wrapper .menu-toggle .bar:nth-child(1) {
  position: relative;
  right: 3px;
}

.page-wrapper .nav-wrapper .menu-toggle .bar:nth-child(2) {
  position: relative;
  left: 5px;
}

.page-wrapper .nav-wrapper .menu-toggle .bar:nth-child(3) {
  position: relative;
  right: 3px;
}

.page-wrapper .nav-wrapper .menu-toggle {
  -ms-grid-column-align: end;
  justify-self: end;
  display: none;
}

.page-wrapper .nav-wrapper .menu-toggle:hover {
  cursor: pointer;
}

.page-wrapper .nav-wrapper #mobile-menu.is-active .bar:nth-child(2) {
  opacity: 0;
  color: white;
}

.page-wrapper .nav-wrapper #mobile-menu.is-active .bar:nth-child(1) {
  -webkit-transform: translateY(8px) rotate(45deg);
  transform: translateY(8px) rotate(45deg);
  color: white;
}

.page-wrapper .nav-wrapper #mobile-menu.is-active .bar:nth-child(3) {
  -webkit-transform: translateY(-8px) rotate(-45deg);
  transform: translateY(-8px) rotate(-45deg);
  color: white;
}

@-webkit-keyframes gradbar {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@keyframes gradbar {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

