    padding: 40px 25px;
    background: white 0% 0% no-repeat padding-box;
    margin: 1em;
    border-radius: 16px;
    cursor: pointer;
    &:hover {
      -webkit-box-shadow: 2px 19px 37px rgba(0, 0, 0, 0.04);
      box-shadow: -6px 19px 37px rgba(0, 0, 0, 0.04);
    }
    .icon__image {
      &.maths {
        background-color: #ebfaff;
      }
      &.physics {
        background-color: #f8e3f6;
      }
      &.biology {
        background-color: #e9f9ec;
      }
      &.chemistry {
        background-color: #ffe4e3;
      }
      &.english {
        background-color: #fff5e3;
      }
    }
  }
}

