.subscriptions {
  background: rgba(16, 163, 255, 0.04) 0% 0% no-repeat padding-box;
  margin-top: 3em;
  padding-top: 5em;
  padding-bottom: 5em;
  .top {
    font-size: 34px;
    font-weight: 600;
    line-height: 40px;
    padding-right: 30px;
    color: #002984;
    margin-top: 100px;
    font-family: "Monsterrat bold";
  }
  .btm {
    font-size: 20px;
    line-height: 1.6;
    padding-right: 30px;
  }
  .band {
    background: #ffffff;
    box-shadow: 0px 8px 23px #0000000a;
    border-radius: 10px;
    padding: 20px 40px;
    min-height: 470px;
    position: relative;
    .name {
      font-size: 44px;
      color: #707070;
    }
    .price {
      color: #707070;
      font-size: 14px;
      span {
        color: #002984;
        font-size: 30px;
        font-weight: 600;
        padding-right: 4px;
        font-family: "Monsterrat bold";
      }
    }
    ul {
      padding-left: 10px;
      margin-top: 30px;
      li {
        font-size: 14px;
        margin-bottom: 5px;
        span {
          color: #707070;
        }
      }
    }
    &.bw-band {
      border-top: 10px solid #10a3ff;
      button {
        background-color: #10a3ff;
        color: #fff;
        padding: 0 30px;
        width: 70%;
        border-radius: 40px;
        position: absolute;
        bottom: -30px;
        left: 15%;
        height: 55px !important;
        font-size: 16px !important;
      }
    }
    &.pp-band {
      border-top: 10px solid #002984;
      button {
        background-color: #002984;
        color: #fff;
        padding: 0 30px;
        width: 70%;
        border-radius: 40px;
        position: absolute;
        bottom: -30px;
        left: 15%;
        height: 55px !important;
        font-size: 16px !important;
      }
    }
  }

  @media (max-width: 768px), (min-width: 768px) and (max-width: 1024px) {
    padding-top: 4em;
    .top {
      padding-right: 0;
      text-align: left;
      font-size: 28px;
      font-weight: 600;
      line-height: 34px;
      margin-top: 0;
    }
    .btm {
      font-size: 20px;
      line-height: 1.6;
      padding-right: 0px;
    }
    .band {
      margin-bottom: 30px;
      .name {
        margin: 20px 0;
      }
    }
  }
}

